.editProfileWrapper{
    display: flex;

    .profileRight {
        flex: 10;

        .profileCover {
            height: 320px;
            position: relative;

            .profileCoverImg {
                width: 100%;
                height: 250px;
                object-fit: cover;
            }

            .profileUserImg {
                position: absolute;
                width: 150px;
                height: 150px;
                border-radius: 50%;
                left: 0;
                right: 0;
                margin: auto;
                top: 150px;
                object-fit: cover;
                border: 2px solid white;

            }

        }

        .profileInfo{
            display: flex;
            flex-direction: column;
            text-align: center;
            justify-content: center;


            .profileInfotName{
                font-size: 24px;
            }

            .profileInfoDesc{
                font-weight: 300;
            }
        }
        .editprofileRightbarBottum{
            display: flex;
            flex-direction: column;

            .top{
                -webkit-box-shadow:2px 4px 10px 1px rgba(0,0,0,0.47);
                box-shadow:2px 4px 10px 1px rgba(201,201,201,0.47);
                margin: 20px;
                padding: 10px;
                display: flex;

                h1{
                    color: #666;
                    font-size: 20px;
                }
            }

            .bottom{
                -webkit-box-shadow:2px 4px 10px 1px rgba(0,0,0,0.47);
                box-shadow:2px 4px 10px 1px rgba(201,201,201,0.47);
                margin: 20px;
                padding: 10px;
                display: flex;
                
                img{
                    width: 200px;
                    height: 200px;
                    border-radius: 50%;
                    margin: 40px 0;
                }
                .left{
                    flex: 1;

            }
                .right{
                    flex: 2;

                    form{
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        margin-left: 50px;

                        .formInput{
                            width: 70%;
                            margin-top:10px;

                            label{
                                display: flex;
                                align-items: center;
                                gap: 5px;

                                .icon{
                                    cursor: pointer;
                                }
                            }

                            input{
                                width:100%;
                                font-size: 18px;
                                padding: 5px;
                                border: none;
                                border-bottom: 1px solid gray ;

                                &:focus{
                                    outline: none;
                                }
                            }
                        }
                        .updateButton{
                            width: 150px;
                            padding: 10px;
                            background-color:#011631;
                            color: white;
                            border: none;
                            font-weight: bold;
                            margin-top: 20px;
                            cursor: pointer;
                        }
                    }

                }
            }
        }
    }
}

//Responsiveness
@media(max-width:768){
    .editProfileWrapper{
         .profileRight{
             .editprofileRightbarBottum{
                .bottom{
                    .left{
                        img{
                            width: 150px;
                            height: 150px;
                            border-radius: 50%;
                        }
                    }
                    .right{
                        flex: 2;

                        form{
                            .formInput{
                                width: 90%;
                            }
                        }
                    }
                }
             }
         }
    }
}

@media(max-width:576){
    .editProfileWrapper{
         .profileRight{
             .editprofileRightbarBottum{
                .bottom{
                    .left{
                        img{
                            width: 100px;
                            height: 100px;
                            border-radius: 50%;
                        }
                    }
                    .right{
                        form{
                            .formInput{
                                width: 80%;
                            }
                        }
                    }
                }
             }
         }
    }
}